.container {
   display: flex;
   justify-content: space-evenly;
   align-items: center;
   height: 100vh;
}

.calendar_container {
   display: flex;
   flex-direction: column;
   align-items: center;
   font-family: tway;
}

.react-calendar {
   width: 350px;
   max-width: 100%;
   font-size: 16px;
   background: white;
   border: 1px solid #a0a096;
   border-radius: 3%;
   font-family: tway, Arial, Helvetica, sans-serif;
   line-height: 1.125em;
}
.react-calendar--doubleView {
   width: 700px;
}
.react-calendar--doubleView .react-calendar__viewContainer {
   display: flex;
   margin: -0.5em;
}
.react-calendar--doubleView .react-calendar__viewContainer > * {
   width: 50%;
   margin: 0.5em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
   -moz-box-sizing: border-box;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}
.react-calendar button {
   margin: 0;
   border: 0;
   outline: none;
}
.react-calendar button:enabled:hover {
   cursor: pointer;
}
.react-calendar__navigation {
   display: flex;
   height: 44px;
   margin-bottom: 1em;
   background: none;
}

.react-calendar__navigation button {
   min-width: 44px;
   background: none;
   color: #f87171;
   font-size: 16px;
   font-weight: 600;
}

.react-calendar__navigation__label span {
   font-size: 15px;
   font-weight: 800;
   font-family: tway;
}

.react-calendar__navigation button:disabled {
   background-color: #ffff;
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
   background-color: #e6e6e6;
}
.react-calendar__month-view__weekdays {
   text-align: center;
   text-transform: uppercase;
   font-weight: bold;
   font-size: 0.75em;
}
.react-calendar__month-view__weekdays__weekday {
   padding: 0.5em;
   font-size: 12px;
   font-weight: 300;
   margin-bottom: 10px;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 0.75em;
   font-weight: bold;
}

.react-calendar__month-view__days__day--weekend {
   color: #d10000;
}

.react-calendar__month-view__days__day--neighboringMonth {
   color: #757575;
}
.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
   padding: 2em 0.5em;
}
.react-calendar__tile {
   max-width: 100%;
   padding: 10px 6.6667px;
   background: none;
   text-align: center;
   line-height: 16px;
}
.react-calendar__tile:disabled {
   background-color: #f0f0f0;
}
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
   background-color: #e6e6e6;
}
.react-calendar__tile--now {
   background: #eecda3; /* fallback for old browsers */
   background: -webkit-linear-gradient(to left, #ef629f, #eecda3); /* Chrome 10-25, Safari 5.1-6 */
   background: linear-gradient(
      to left,
      #ef629f,
      #eecda3
   ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
   background: #eecda3; /* fallback for old browsers */
   background: -webkit-linear-gradient(to left, #ef629f, #eecda3); /* Chrome 10-25, Safari 5.1-6 */
   background: linear-gradient(
      to left,
      #ef629f,
      #eecda3
   ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.react-calendar__tile--hasActive {
   background: #76baff;
}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
   background: #a9d4ff;
}
.react-calendar__tile--active {
   background: #bbd2c5; /* fallback for old browsers */
   background: -webkit-linear-gradient(to right, #292e49, #536976, #bbd2c5); /* Chrome 10-25, Safari 5.1-6 */
   background: linear-gradient(
      to right,
      #292e49,
      #536976,
      #bbd2c5
   ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

   color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
   background: #bbd2c5; /* fallback for old browsers */
   background: -webkit-linear-gradient(to right, #292e49, #536976, #bbd2c5); /* Chrome 10-25, Safari 5.1-6 */
   background: linear-gradient(
      to right,
      #292e49,
      #536976,
      #bbd2c5
   ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.react-calendar--selectRange .react-calendar__tile--hover {
   background-color: #e6e6e6;
}

.reserve {
   width: 40px;
   height: 20px;
   font-size: 10px;
   font-weight: 700;
   color: black;
}

.absoluteDiv {
   position: relative;
   display: flex;
   justify-content: center;
   align-items: center;
}

.line {
   position: absolute;
   width: 20px;
   border: 1px gray solid;
   margin: 0 auto;
   top: -10px;
}

@font-face {
   font-family: 'tway';
   src: url('./fonts/tway_air.ttf');
}

@media (max-width: 767px) {
   .container {
      flex-direction: column;
   }

   .react-calendar {
      width: 300px;
   }

   .react-calendar__month-view__weekdays__weekday {
      font-size: 10px;
   }

   .react-calendar__tile {
      max-width: 50%;
      height: 45px;
   }

   .react-calendar__navigation {
      height: 20px;
   }

   .react-calendar__navigation button {
      font-size: 10px;
   }

   .react-calendar__navigation__label span {
      font-size: 14px;
   }

   .reserve {
      width: 23px;
      height: 12px;
      font-size: 6px;
   }
}
